<template>
  <div class="items">
    <div class="item" v-for="(item, index) in oData">
      <video controls="controls" width="100%" :poster="item.cover">
        <source :src="item.url"></video>
      </video>
      <!-- 动态设置路由跳转 -->
      <p @click="toDetail('/videoDetail', item.id)">{{item.id}}</p>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      oData: [
        {
          'url': 'http://yeeduo.com/video/01.mp4',
          'cover': 'http://static.cometochina.com/images/trip/56f77cfba037f3092c6bd8c2e829ff55.JPG',
          'id': '1'
        },
        {
          'url': 'http://yeeduo.com/video/02.mp4',
          'cover': 'http://static.cometochina.com/images/trip/f6406014558973e5c53ae56184713d03.jpg',
          'id': '2'
        },
        {
          'url': 'http://yeeduo.com/video/03.mp4',
          'cover': 'http://static.cometochina.com/images/advert/e80ba379cc2c6e25f1e54bccf708c362.jpg',
          'id': '3'
        },
        {
          'url': 'http://yeeduo.com/video/04.mp4',
          'cover': 'http://static.cometochina.com/images/advert/e80ba379cc2c6e25f1e54bccf708c362.jpg',
          'id': '4'
        }
      ]
    }
  },
  methods: {
    // 点击带参数（对应显示的详情页id）传递
    toDetail (path, id) {
      this.$router.push({
        path: path,
        query: {
          id: id
        }
      })
    }
  }
}
</script>
<style scoped lang="scss">
  .item {
    width: 10rem;
  }
</style>



